.field-shape {
	display: flex;
	gap: var(--gap-s);
}

.field-shape__button {
	background: transparent;
	font-size: 0;
	padding: var(--gap-s);
	vertical-align: middle;
	transition: background-color .1s linear;
	border-radius: 3px;
	cursor: pointer;
}

.field-shape__button:hover,
.field-shape__button:focus,
.field-shape__button--selected {
	background: rgba(var(--shading), .1);
}

.field-shape__preview {
	--size: 1.5rem;
	width: var(--size);
	height: var(--size);
	fill: rgba(var(--shading), .4);
}

.field-shape__button--selected > .field-shape__preview {
	fill: rgb(var(--accent));
}
