.field-set-editor {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	align-items: stretch;
	gap: var(--gap-m);
	padding: var(--gap-l);
	--field-height: 2.8rem;
}

.field-set-editor__label {
	display: block;
	margin: 0 0 var(--gap-s) 0;
	font-size: .8rem;
	color: rgba(var(--text), .8);
	transition: color .2s linear;
}

.field-set-editor__field:focus-within > .field-set-editor__label {
	color: rgb(var(--accent));
}
