.popup {
	position: absolute;
	padding: var(--gap-m) 0;
	background-color: rgb(var(--base));
	background-image: linear-gradient(rgba(var(--shading), .1), rgba(var(--shading), .1));
	border-radius: 3px;

	transform: scale3d(1, 0, 1);
	transform-origin: center top;
	opacity: 0;
	visibility: hidden;
	transition:
		opacity .2s linear .2s,
		transform .2s ease .2s,
		visibility 0s linear .4s;
}

.popup--open {
	transform: scale3d(1, 1, 1);
	opacity: 1;
	visibility: visible;
	transition-delay: 0s;
}

.popup__content {
	opacity: 0;
	transition: opacity .2s linear;
}

.popup--open > .popup__content {
	opacity: 1;
	transition-delay: .2s;
}
