.field-select {
	position: relative;
	background: rgba(var(--shading), .1);
	border-radius: 3px;
}

.field-select__badge {
	display: block;
	box-sizing: border-box;
	width: 100%;
	height: var(--field-height);
	padding: var(--gap-m);
	background: transparent;
	color: inherit;
	text-align: left;
	cursor: pointer;
}

.field-select__popup {
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
}

.field-select__list {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	align-items: stretch;
}

.field-select__option {
	padding: var(--gap-m);
	text-align: left;
	background: transparent;
	transition: background-color .1s linear;
	cursor: pointer;
}

.field-select__option--selected {
	color: rgb(var(--accent));
	background: rgba(var(--shading), .05);
}

.field-select__option:hover,
.field-select__option:focus {
	background: rgba(var(--shading), .1);
}

/* arrow */
.field-select::after {
	--size: .5rem;
	content: ' ';
	position: absolute;
	top: calc((var(--field-height) - var(--size)) / 2);
	right: var(--gap-m);
	width: var(--size);
	height: var(--size);
	pointer-events: none;
	transform: rotate3d(0, 0, 1, 135deg);
	transition: transform .5s ease;
	border-radius: 2px;
	opacity: .7;
	background:
		linear-gradient(to left, rgb(var(--shading)) 20%, transparent 20%),
		linear-gradient(to bottom, rgb(var(--shading)) 20%, transparent 20%);
}

.field-select--open::after {
	transform: rotate3d(0, 0, 1, -45deg);
}
