/* @import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap'); */

* {
	font: inherit;
	color: inherit;
}

html {
	font-family: 'Source Sans Pro', sans-serif;
	/* line-height: 1.4;
	font-size: 14px;
	text-size-adjust: 100%; */

	/* base background for UI components, should not be used with alpha */
	--base: 250, 250, 250;

	/* color to shade parts of UI with, best used with alpha and layering */
	--shading: 0, 0, 0;

	/* color to use for text, sometimes used with alpha to dim text, should be the same hue as --shading */
	--text: 50, 50, 50;

	/* accent color for UI components such as default buttons, should not be used with alpha */
	--accent: 0, 204, 156;

	/* three levels of padding/margin */
	--gap-s:  .25rem;
	--gap-m:  .75rem;
	--gap-l: 1.5rem;
}

@media (prefers-color-scheme: dark) {
	html {
		--base: 42, 42, 42;
		--text: 240, 240, 240;
		--shading: 255, 255, 255;
		--accent: 0, 204, 156;
	}
}

