.field-number {
	position: relative;
	display: flex;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: center;
	box-sizing: border-box;
	height: var(--field-height);
	padding: var(--gap-m);
	background: rgba(var(--shading), .1);
	border-radius: 3px;
	cursor: text;
}

.field-number__input-wrapper {
	position: relative;
}

.field-number__input,
.field-number__text {
	font-family: monospace;
	font-size: 1.1rem;
}

.field-number__input {
	position: absolute;
	inset: 0;
	background: transparent;
	color: inherit;
}

.field-number__text {
	white-space: pre;
	color: rgba(var(--text), .15);
}

.field-number__phantom {
	visibility: hidden;
}

.field-number__sign {
	cursor: pointer;
}

.field-number__sign--negative {
	color: inherit;
}
